// HEADER (–®–ê–ü–ö–ê) =======================================================================================
.header {
  .header-container {
    padding: 1% 3%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
  }

  // –õ–û–ì–û
  .header__logo {
    display: flex;
    .header__img {
      width: 50px;
      height: 50px;
      margin: -10px 10px -10px -10px;
    }
    .header__link {
      display: flex;
      height: 30px;
      font-weight: bold;
      font-size: 30px;
      &:hover {
        color: gold;
        color: --DarkRed;
      }
    }
  }
  &__menu {
    display: flex;
    flex-direction: column;
  }

  // –í–ï–†–•–ù–ï–ï –ú–ï–ù–Æ -------------------------------------------------
  &__menu-top {
    // .menu-top__span {
