    }
    // –∑–∞—Ç–µ–º–Ω—è–µ—Ç —Å–æ—Å–µ–¥. —ç–ª. –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ (–ø–æ–¥–º–µ–Ω—é)
    &:hover li:not(:hover) {
      opacity: 0.3;
      color: white !important;
    }
    .menu-top__items {
      position: relative;
      margin-left: 15px;
      margin-bottom: 5px;

      // –ù–ê–í–ï–î–ï–ù–ò–ï –Ω–∞ –¥–æ—á–µ—Ä–Ω–∏–µ —ç–ª-—Ç—ã
      &:hover {
        // —Å—Å—ã–ª–∫–∏
        // .m-t-items__navlink {
        // }
        // –ø–æ–¥–º–µ–Ω—é –ø—Ä–∏ –Ω–µ–≤–µ–¥–µ–Ω–∏–∏
        .m-t-items__ul {
          opacity: 1;
          visibility: visible;
          transform: translate(0px, 0px);
          pointer-events: all;
        }
      }

      // –≤–∏–¥–∏–º—ã–µ —Å—Å—ã–ª–∫–∏ –≤ header --------------------------------------------------------------------
      // NavLink (–Ω–∞ —Ä–æ–¥–∏—Ç–µ–ª—è) - –≤–∏–¥–∏–º—ã–π —ç–ª. header (–µ/–∏ –≤ —Ä–æ–¥–∏—Ç–µ–ª–µ –µ—Å—Ç—å <–∞> —Å .active) (—Å–ª–æ–∂–Ω. –ø–µ—Ä–µ–Ω–µ—Å–µ–Ω–æ –Ω–∞ NavLink —Å–≤–æ–π –∫–ª–∞—Å—Å)
      // &:has(a[class$="active"]) {
      // }
      // NavLink (—Å–≤–æ–π –∫–ª–∞—Å –Ω–∞ —Å—Å—ã–ª–∫—É) - –≤–∏–¥–∏–º—ã–π —ç–ª. header (–ø–µ—Ä–µ–Ω–µ—Å–µ–Ω–æ –≤ projects.scss(–æ–±—â.—Å—Ç–∏–ª–∏ –ø—Ä–æ–µ–∫—Ç–∞) –∫ —Å—Å—ã–ª–∫–∞–º)
      // .m-t-items__navlink {
      //   &.active {
      //   }
      //   &:hover {
      //   }
      // –ø–æ–¥–º–µ–Ω—é –ø—Ä–∏ –Ω–µ–≤–µ–¥–µ–Ω–∏–∏
      //   .m-t-items__ul {
      //   }
      // }

      // –ü–û–î–ú–ï–ù–Æ
      .m-t-items__ul {
        // —á–∞—Å—Ç–∏—á–Ω–æ –∏–∑ –ï–∂–µ–°–≤–µ—Ç
        width: 100%;
        position: absolute;
        padding: 1px 3px 3px;
        opacity: 0;
        visibility: hidden;
        font-size: 16px;
        text-align: center;
        transform: translate(0px, 10%);
