/* –ê–ù–ò–ú–ê–¶–ò–Ø –°–¢–†–ê–ù–ò–¶ (2 —Å–ø–æ—Å–æ–±–∞ - .page|pages) ======================================================================================= */
.page,
.pages {
  /* position: absolute; */
  left: 15px;
  right: 15px;
  overflow: hidden;

  /* 1 —Å–ø–æ—Å–æ–± –ø—Ä–æ–ø–∏—Å–∞—Ç—å –∞–Ω–∏–º–∞—Ü–∏—é (.page - –Ω–∞ –∫–∞–∂–¥–æ–µ –¥–µ–π—Ç–≤–∏–µ 4—à—Ç) */
  .page-enter {
    opacity: 0;
    transform: scale(1.1);
    /* transform: translateX(-100%); */
  }
  .page-enter-active {
    opacity: 1;
    transform: scale(1);
    /* transform: translateX(0); */
    transition: opacity 500ms, transform 500ms;
  }
  .page-exit {
    opacity: 1;
    transform: scale(1);
    /* transform: translateX(0); */
  }
  .page-exit-active {
    opacity: 0;
    transform: scale(0.9);
    /* transform: translateX(100%); */
    transition: opacity 500ms, transform 500ms;
  }

  /* 2 —Å–ø–æ—Å–æ–± –ø—Ä–æ–ø–∏—Å–∞—Ç—å –∞–Ω–∏–º–∞—Ü–∏—é (.pages - –Ω–∞ –¥–≤–∞ –¥–µ–π—Ç–≤–∏–µ. –≤—Å–µ–≥–æ 4—à—Ç) */
  .pages-enter-active {
    /* .pages-enter { */
    animation: fade-in 500ms forwards;
  }
  .pages-exit-active {
    /* .pages-exit { */
    animation: fade-out 500ms forwards;
  }
  @keyframes fade-in {
    0% {
      opacity: 0;
      transform: translateX(-100%);
    }
    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes fade-out {
    0% {
      opacity: 1;
      transform: translateX(0);
    }
    100% {
      opacity: 0;
      transform: translateX(100%);
    }
  }
}
