// –º–∏–∫—Å–∏–Ω - –∫–æ–º–ø–ª–µ–∫—Ç —Å–≤–æ–π—Å—Ç–≤, —Å —Ä–∞–∑–Ω—ã–º–∏ –ø—Ä–µ—Ñ–∏–∫—Å–∞–º–∏(–¥–ª—è —Ä–∞–∑–Ω—ã—Ö –±—Ä–∞—É–∑–µ—Ä–æ–≤), –≤ –∫–æ—Ç–æ—Ä—ã–µ –º–æ–∂–Ω–æ –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—Ç—å –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ (–ú–ù–û–ì–û mixin–æ–≤ —É–¥–∞–ª–∏–ª –∑–∞ –Ω–µ–≤–æ—Å—Ç—Ä–µ–±–æ–≤–∞–Ω–æ—Å—Ç—å—é)

// –§—Ä–∏–ü–æ–ñ–∏–∑–Ω ==================================================================================
// –ê–î–ê–ü–¢–ò–í–ù–´–ï –°–í–û–ô–°–¢–í–ê ----------------------------------------------------------------------------------
// –ü–æ–¥–∫–ª—é—á–µ–Ω–∏–µ —à—Ä–∏—Ñ—Ç–∞
@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    src: url("../fonts/#{$file_name}.woff2") format("woff2"),
      url("../fonts/#{$file_name}.woff") format("woff");
    font-weight: #{$weight};
    font-style: #{$style};
  }
}
// –†–∞–∑–º–µ—Ä –≤ –æ–¥–Ω—É —Å—Ç—Ä–æ–∫—É. @include size(80px, 50px);
@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}
// –ê–¥–∞–ø—Ç–∏–≤–Ω—ã–π —à—Ä–∏—Ñ—Ç. @include adaptiv-font(24, 12);
@mixin adaptiv-font($pcSize, $mobSize) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  font-size: calc(
    #{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth})
  );
}

//  Vedeev ===================================================================================
// px –≤ rem. –ø–µ—Ä–µ–º–µ–Ω. $mainFontSize –∏–∑ vars.scss –¥–µ–ª–∏—Ç –Ω–∞ –ø–µ—Ä–µ–¥–∞–Ω—ã–µ px –ø—Ä–µ–≤—Ä–∞—â –≤ rem –∏ –∞–¥–∞–ø—Ç–∏—Ä—É–µ—Ç –ø–æ–¥ —ç–∫—Ä–∞–Ω —Å –ø–æ–º–æ—â—å—é @media –∏–∑ main.scss(font-size: rem(20);)
@function rem($pixels, $context: $mainFontSize) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }
  @if (unitless($context)) {
    $context: $context * 1px;
  }
  // ??? –Ω–µ —Ä–∞–± - —É—Å—Ç–∞—Ä –º–µ—Ç–æ–¥. –ø–µ—Ä–µ–ø–∏—Å–∞—Ç—å –Ω–∞ math.div
  // ! –ø–µ—Ä–µ–ø–∏—Å–∞–ª - –Ω–µ –ø—Ä–æ–≤–µ—Ä—è–ª
  // @return math.div(($pixels, $context) * 1rem);
  // @return $pixels / $context * 1rem;
  // @return math.div($pixels, $context * 1rem);
  @return math($pixels, $context * 1);
}

//  –°–í–û–Å ===================================================================================
// –ê–¥–∞–ø—Ç–∏–≤–Ω–æ–µ —Å–≤–æ–π—Å—Ç–≤–æ(—Å–≤–æ-–≤–æ, –Ω–∞—á,—Å–µ—Ä,–∫–æ–Ω–µ—á –∑–Ω–∞—á–Ω). @include adaptiv-val("padding-right", 32, 15);
@mixin adaptiv-val($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: calc(
    #{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320})
  );
}
// –ê–¥–∞–ø—Ç–∏–≤–Ω–æ–µ —Å–≤–æ–π—Å—Ç–≤–æ –¥–ª—è –Ω–µ—Å–∫ –∑–Ω–∞—á–µ–Ω–∏–π. @include adaptiv-value-all("padding", 10, 5, 30, 10, 60, 25);
// 1–æ–µ - —Å–≤–æ-–≤–æ, –¥–≤–µ –ø–µ—Ä–≤—ã–µ —Ü–∏—Ñ—Ä—ã - –Ω–∞—á–∞–ª—å–Ω–æ–µ –∏ –∫–æ–Ω–µ—á–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –¥–ª—è –≤–µ—Ä—Ö–Ω.–ø–∞—Ä–∞–º., 2–µ –ø–æ —Å–µ—Ä–µ–¥–∏–Ω–µ - –Ω–∞—á./–∫–æ–Ω–µ—á.–∑–Ω–∞—á. –¥–ª—è –≥–æ—Ä–∏–Ω—Ç.–ø–∞—Ä–∞–º, 2–µ –ø–æ—Å–ª–µ–¥.—Ü–∏—Ñ—Ä—ã - –Ω–∞—á./–∫–æ–Ω–µ—á.–∑–Ω–∞—á. –¥–ª—è –Ω–∏–∑–∞
@mixin adaptiv-value-all(
  $property,
  $startSizeH,
  $minSizeH,
  $startSizeW,
  $minSizeW,
  $startSizeHBott,
  $minSizeHBott
) {
  // @if $type==1 {
  $addSizeHTop: $startSizeH - $minSizeH;
  $addSizeW: $startSizeW - $minSizeW;
  $addSizeHBott: $startSizeHBott - $minSizeHBott;

  #{$property}-top: calc(
    #{$minSizeH + px} + #{$addSizeHTop} * ((100vw - 320px) / #{$maxWidth - 320})
  );
  #{$property}-bottom: calc(
    #{$minSizeHBott + px} + #{$addSizeHBott} *
      ((100vw - 320px) / #{$maxWidth - 320})
  );
  #{$property}-left: calc(
    #{$minSizeW + px} + #{$addSizeW} * ((100vw - 320px) / #{$maxWidth - 320})
  );
  #{$property}-right: calc(
    #{$minSizeW + px} + #{$addSizeW} * ((100vw - 320px) / #{$maxWidth - 320})
  );
  // ??? –Ω–µ —Ä–∞–± - –ø–æ–ø—ã—Ç–∫–∞ –Ω–µ—Å–∫. —É—Å–ª–æ–≤–∏–π –≤ –æ—à–±. -  @if $startSizeHBott {} @else {} - –æ—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∞—Ä–≥—É–º–µ–Ω—Ç—ã
}
